  background: transparent url(../img/general/close.svg) center no-repeat;
  background-size: 14px 14px;
}

.header__menu {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

@media (min-width: 768px) {
  .header__menu {
    display: flex;
  }
}

.header__item {
  position: relative;
  margin-bottom: 4px;
  text-align: center;
}

@media (min-width: 768px) {
  .header__item {
    padding-top: 12px;
    padding-bottom: 12px;
  }
}

@media (min-width: 768px) {
  .header__item:hover .header__link--dropdown::before {
    transform: rotate(-180deg);
    visibility: visible;
  }
}

@media (min-width: 768px) {
  .header__item:hover .header__link--dropdown {
    background-color: #dc6502;
    color: #fff;
  }
}

.header__item:hover .header__dropdown {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

@media (min-width: 768px) {
  .header__item {
    margin-bottom: 0;
  }
}

.header__item--active .header__link {
  background-color: #fd7e14;
  border-radius: 16px;
}

.header__item--active .header__link:hover {
  background-color: #dc6502;
  color: #fff;
}

@media (min-width: 768px) {
  .header__item--active .header__link:hover {
    background-color: transparent;
    color: #fd7e14;
  }
}

@media (min-width: 768px) {
  .header__item--active .header__link {
    background-color: transparent;
  }
}

.header__link, .header__dropdown-link {
  display: inline-block;
  vertical-align: top;
  padding: 6px 23px;
  color: #fff;
  font-size: 11px;
  font-weight: bold;
  line-height: 1.82;
  letter-spacing: 0.6px;
  text-transform: uppercase;
  text-align: center;
}

@media (min-width: 768px) {
  .header__link, .header__dropdown-link {
    letter-spacing: 0;
  }
}

.header__link {
  border-radius: 16px;
}

.header__link--dropdown::before {
  content: '';
  position: absolute;
  right: 10px;
  top: 50%;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 5px 4px 0 4px;
  border-color: #ffffff transparent transparent transparent;
  transform: translateY(-50%);
  opacity: 1;
  visibility: hidden;
  z-index: 1;
  transform-origin: top center;
}

@media (min-width: 768px) {
  .header__link--dropdown::before {
    opacity: 1;
    visibility: visible;
  }
}

.header__dropdown {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.header__dropdown::before {
  content: '';
  position: absolute;
  top: -8px;
  left: 18px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 9px 8px 9px;
  border-color: transparent transparent #ffffff transparent;
  opacity: 0;
  visibility: hidden;
}

@media (min-width: 768px) {
  .header__dropdown::before {
    opacity: 1;
    visibility: hidden;
  }
}

@media (min-width: 768px) {
  .header__dropdown {
    position: absolute;
    top: 100%;
    min-width: 224px;
    padding-top: 12px;
    padding-bottom: 12px;
    box-shadow: 0 0 15px 0 #00000026;
    background-color: #ffffff;
    opacity: 0;
    visibility: hidden;
    transform: translateY(-10px);
    transition: all 0.2s ease-in-out;
  }
}

@media (min-width: 768px) {
  .header__dropdown-item {
    text-align: left;
  }
}

@media (min-width: 768px) {
  .header__dropdown-link {
    color: #707070;
  }
}

.header__toggle-menu {
  position: absolute;
  right: 12px;
  top: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border: none;
  background-color: transparent;
  transform: translateY(-50%);
}

.header__icon-burger {
  display: block;
  height: 2px;
  width: 16px;
  background-color: #fff;
}

.header__icon-burger::before, .header__icon-burger::after {
  content: '';
  position: absolute;
  width: 16px;
  height: 2px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #fff;
}

.header__icon-burger::before {
  top: 13px;
}

.header__icon-burger::after {
  bottom: 13px;
}

.header__contacts {
  display: flex;
  align-items: center;
  margin-left: auto;
}

@media (min-width: 768px) {
  .header__contacts {
    margin-left: 0;
  }
}

.header__tel {
  margin-right: 12px;
  font-size: 18px;
  font-weight: 500;
  color: #ffffff;
}

@media (min-width: 768px) {
  .header__tel {
    margin-right: 0;
    font-size: 16px;
    padding-right: 24px;
    padding-left: 16px;
    font-weight: 400;
  }
}

@media (min-width: 1216px) {
  .header__tel {
    padding-right: 36px;
    padding-left: 36px;
    font-size: 24px;
  }
}

.header__order-call {
  width: 40px;
  height: 40px;
  background: transparent url(../img/general/phone.svg) no-repeat center;
  background-size: 17px 17px;
  border: none;
}

@media (min-width: 768px) {
  .header__order-call {
    width: auto;
    height: auto;
    min-width: 160px;
    background-color: #fd7e14;
    background-image: none;
  }
}

@media (min-width: 1216px) {
  .header__order-call {
    min-width: 194px;
  }
}
